:root {
    --black: rgb(0,0,0);
    --white: rgb(255,255,255);
    --red: rgb(153, 0, 0);
    --blue: rgb(0, 0, 193);
    --dark-grey: rgb(40,44,52);
    --dark-grey80: rgba(40,44,52,0.8);
    --dark-grey50: rgba(40,44,52,0.5);
    --dark-grey10: rgba(40,44,52,0.1);
    --size-default: 16px;
    --size-medium: 36px;
    --size-large: 48px;
}


.main {
    font-family:Arial, Helvetica, sans-serif;
    font-size: var(--size-default);
}


